.element__like-button {
    width: 22px;
    height: 19px;
    background-image: url(../../../images/like-button.svg);
    background-size: cover;
    background-position: center;
    border: none;
    background-color: #fff;
}

.element__like-button:hover {
    opacity: 0.5;
    cursor: pointer;
}